import React from 'react';
import Slider from "react-slick";
import slide_one from '../images/image_one.jpg';
import slide_two from '../images/image_two.jpg';
import slide_three from '../images/image_three.jpg';

const Carrousel = () => {

    const settings = {
        dots: false,
        infinite: true,
        autoplay: true,
        speed: 500
      };

    return (
        <div
            className="carrousel_wrapper"
        >
            <Slider {...settings}>

                <div>
                    <div
                        className="carrousel_image"
                        style={{
                            background:`url(${slide_one})`,
                            height: '400px'
                        }}
                    >
                    </div>
                </div>
                <div>
                    <div
                        className="carrousel_image"
                        style={{
                            background:`url(${slide_two})`,
                            height: '400px'
                        }}
                    >
                    </div>
                </div>
                <div>
                    <div
                        className="carrousel_image"
                        style={{
                            background:`url(${slide_three})`,
                            height: '400px'
                        }}
                    >
                    </div>
                </div>
            </Slider>
        </div>
    );
};

export default Carrousel;